"use client";

import { useState, useMemo, useEffect, useRef } from "react";

// Score-based color function
function getScoreColor(score: number): string {
    if (score >= 90) return '#10b981'; // Exceptional - green
    if (score >= 85) return '#3b82f6'; // Strong - blue
    if (score >= 80) return '#22c55e'; // Good - green
    if (score >= 70) return '#f59e0b'; // Needs work - amber
    return '#ef4444'; // Risk - red
}

type TabId = "overview" | "fixes" | "sections" | "rewrites" | "alignment" | "wins";

interface ReportData {
    score?: number;
    score_label?: string;
    score_comment_short?: string;
    score_comment_long?: string;
    summary?: string;
    subscores?: {
        impact?: number;
        clarity?: number;
        story?: number;
        readability?: number;
    };
    first_impression?: string;
    strengths?: string[];
    gaps?: string[];
    top_fixes?: Array<{ fix?: string; text?: string; impact_level?: string; effort?: string; section_ref?: string }>;
    section_review?: Record<string, { working?: string | null; missing?: string | null; fix?: string | null; grade?: string; priority?: string }>;
    rewrites?: Array<{ original: string; better: string; label?: string; enhancement_note?: string }>;
    job_alignment?: {
        strongly_aligned?: string[];
        underplayed?: string[];
        missing?: string[];
        role_fit?: {
            best_fit_roles?: string[];
            stretch_roles?: string[];
            seniority_read?: string;
            industry_signals?: string[];
            company_stage_fit?: string;
        };
        positioning_suggestion?: string;
    };
    ideas?: {
        questions?: Array<{ question: string; archetype?: string; why?: string }>;
        notes?: string[];
        how_to_use?: string;
    };
    next_steps?: string[];
}

interface ReportPanelProps {
    report: ReportData | null;
    isLoading: boolean;
    hasJobDescription: boolean;
}

const tabs: { id: TabId; label: string }[] = [
    { id: "overview", label: "Overview" },
    { id: "fixes", label: "Top Fixes" },
    { id: "sections", label: "By Section" },
    { id: "rewrites", label: "Bullet Upgrades" },
    { id: "alignment", label: "Job Alignment" },
    { id: "wins", label: "Missing Wins" }
];

// Score band logic matching main.js getScoreMeta
function getScoreBand(score: number): { label: string; colorClass: string } {
    if (score >= 90) return { label: "EXCEPTIONAL", colorClass: "band-exceptional" };
    if (score >= 85) return { label: "STRONG", colorClass: "band-strong" };
    if (score >= 80) return { label: "GOOD", colorClass: "band-good" };
    if (score >= 70) return { label: "NEEDS WORK", colorClass: "band-needs-work" };
    return { label: "RISK", colorClass: "band-risk" };
}

export default function ReportPanel({ report, isLoading, hasJobDescription }: ReportPanelProps) {
    const [activeTab, setActiveTab] = useState<TabId>("overview");

    const showEmptyState = !report && !isLoading;
    const showLoading = isLoading;
    const showReport = report && !isLoading;

    // DEBUG: Log render conditions
    console.log("[ReportPanel] Render conditions:", {
        report: !!report,
        reportScore: report?.score,
        isLoading,
        showEmptyState,
        showLoading,
        showReport,
        activeTab
    });

    // Compute subscore bands
    const subscoreBands = useMemo(() => {
        if (!report?.subscores) return null;
        return {
            impact: report.subscores.impact ? getScoreBand(report.subscores.impact) : null,
            clarity: report.subscores.clarity ? getScoreBand(report.subscores.clarity) : null,
            story: report.subscores.story ? getScoreBand(report.subscores.story) : null,
            readability: report.subscores.readability ? getScoreBand(report.subscores.readability) : null
        };
    }, [report?.subscores]);

    // First impression text fallback
    const firstImpressionText = report?.score_comment_long || report?.score_comment_short || report?.first_impression || report?.summary;

    // Animated score dial state
    const [animatedScore, setAnimatedScore] = useState(0);
    const animationRef = useRef<number | null>(null);

    // Animate score when report changes
    useEffect(() => {
        const targetScore = report?.score || 0;
        if (targetScore <= 0) {
            setAnimatedScore(0);
            return;
        }

        const duration = 1200; // 1.2 seconds
        const startTime = Date.now();

        const animate = () => {
            const elapsed = Date.now() - startTime;
            const progress = Math.min(elapsed / duration, 1);
            // Ease-out cubic
            const eased = 1 - Math.pow(1 - progress, 3);
            const current = Math.round(targetScore * eased);
            setAnimatedScore(current);

            if (progress < 1) {
                animationRef.current = requestAnimationFrame(animate);
            } else {
                setAnimatedScore(targetScore);
            }
        };

        // Reset and start animation
        setAnimatedScore(0);
        const timeoutId = setTimeout(() => {
            animationRef.current = requestAnimationFrame(animate);
        }, 100);

        return () => {
            clearTimeout(timeoutId);
            if (animationRef.current) {
                cancelAnimationFrame(animationRef.current);
            }
        };
    }, [report?.score]);

    // Current color based on animated score
    const dialColor = getScoreColor(animatedScore);

    return (
        <div className="report-panel">
            {/* Tabs */}
            <div className="report-tabs">
                {tabs.map((tab) => (
                    <button
                        key={tab.id}
                        className={`report-tab ${activeTab === tab.id ? "active" : ""}`}
                        onClick={() => setActiveTab(tab.id)}
                    >
                        {tab.label}
                    </button>
                ))}
            </div>

            {/* Report Content */}
            <div className="report-content">
                {/* Empty State */}
                {showEmptyState && (
                    <div className="report-empty">
                        <svg className="report-empty-icon" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5"
                                d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                        </svg>
                        <div className="report-empty-title">Ready when you are</div>
                        <div className="report-empty-text">Upload or paste your resume to get recruiter-grade feedback in seconds.</div>
                    </div>
                )}

                {/* Loading State */}
                {showLoading && (
                    <div className="report-loading visible">
                        <div className="loading-spinner" />
                        <div className="loading-text">Reading your resume like a recruiter...</div>
                        <div className="loading-subtext">This usually takes 10-15 seconds</div>
                    </div>
                )}

                {/* Report Sections */}
                {showReport && (
                    <>
                        {/* Overview */}
                        {activeTab === "overview" && (
                            <div className="report-section active" style={{ width: '100%' }}>
                                <div style={{
                                    display: 'flex',
                                    alignItems: 'flex-start',
                                    gap: '32px',
                                    padding: '24px',
                                    background: 'var(--bg-card, #fff)',
                                    border: '1px solid var(--border-subtle, #e2e8f0)',
                                    borderRadius: '12px',
                                    marginBottom: '24px'
                                }}>
                                    {/* Score Dial - 100% inline styles */}
                                    <div style={{
                                        display: 'flex',
                                        flexDirection: 'column',
                                        alignItems: 'center',
                                        gap: '8px',
                                        flexShrink: 0
                                    }}>
                                        <div style={{ position: 'relative', width: '120px', height: '120px' }}>
                                            <svg viewBox="0 0 120 120" style={{ width: '100%', height: '100%', transform: 'rotate(-90deg)' }}>
                                                <circle
                                                    cx="60" cy="60" r="54"
                                                    fill="none"
                                                    stroke="var(--border-subtle, #e2e8f0)"
                                                    strokeWidth="8"
                                                />
                                                <circle
                                                    cx="60" cy="60" r="54"
                                                    fill="none"
                                                    stroke={dialColor}
                                                    strokeWidth="8"
                                                    strokeLinecap="round"
                                                    strokeDasharray="339.292"
                                                    strokeDashoffset={339.292 * (1 - animatedScore / 100)}
                                                />
                                            </svg>
                                            <div style={{
                                                position: 'absolute',
                                                top: '50%',
                                                left: '50%',
                                                transform: 'translate(-50%, -50%)',
                                                fontFamily: 'var(--font-display, system-ui)',
                                                fontSize: '36px',
                                                fontWeight: 800,
                                                color: dialColor
                                            }}>
                                                {animatedScore}
                                            </div>
                                        </div>
                                        <div style={{
                                            fontSize: '12px',
                                            fontWeight: 500,
                                            color: 'var(--text-muted, #64748b)',
                                            textTransform: 'uppercase',
                                            letterSpacing: '0.04em'
                                        }}>
                                            Score
                                        </div>
                                    </div>
                                    <div className="score-content">
                                        {firstImpressionText && (
                                            <div className="first-10-seconds">
                                                <div className="first-10-header">
                                                    <svg className="first-10-icon" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                        <path d="M12 5C16.478 5 20.268 7.405 22 11C20.268 14.595 16.478 17 12 17C7.522 17 3.732 14.595 2 11C3.732 7.405 7.522 5 12 5Z" stroke="currentColor" strokeWidth="1.5" />
                                                        <circle cx="12" cy="11" r="3" stroke="currentColor" strokeWidth="1.5" />
                                                    </svg>
                                                    <div className="first-10-titles">
                                                        <span className="first-10-title">Recruiter First Impression</span>
                                                    </div>
                                                </div>
                                                <div className="first-10-content">{firstImpressionText}</div>
                                            </div>
                                        )}
                                        <div className="score-benchmark">
                                            <span><span className="benchmark-dot exceptional" /> 90+ Exceptional</span>
                                            <span><span className="benchmark-dot strong" /> 85+ Strong</span>
                                            <span><span className="benchmark-dot good" /> 80+ Good</span>
                                            <span><span className="benchmark-dot needs-work" /> 70+ Needs Work</span>
                                            <span><span className="benchmark-dot risk" /> &lt;70 Risk</span>
                                        </div>
                                    </div>
                                </div>

                                {/* Subscores with Band Labels */}
                                <div className="subscores">
                                    <div className="subscore" data-tooltip="Do your bullets show measurable outcomes and meaningful results?">
                                        <div className="subscore-value">{report.subscores?.impact || "‚Äî"}</div>
                                        <div className="subscore-label">Impact ‚ìò</div>
                                        {subscoreBands?.impact && (
                                            <span className={`subscore-band ${subscoreBands.impact.colorClass}`}>
                                                {subscoreBands.impact.label}
                                            </span>
                                        )}
                                    </div>
                                    <div className="subscore" data-tooltip="Is your language direct and jargon-free? Can a recruiter quickly understand what you did?">
                                        <div className="subscore-value">{report.subscores?.clarity || "‚Äî"}</div>
                                        <div className="subscore-label">Clarity ‚ìò</div>
                                        {subscoreBands?.clarity && (
                                            <span className={`subscore-band ${subscoreBands.clarity.colorClass}`}>
                                                {subscoreBands.clarity.label}
                                            </span>
                                        )}
                                    </div>
                                    <div className="subscore" data-tooltip="Does your resume tell a coherent career narrative? Is your professional identity clear?">
                                        <div className="subscore-value">{report.subscores?.story || "‚Äî"}</div>
                                        <div className="subscore-label">Story ‚ìò</div>
                                        {subscoreBands?.story && (
                                            <span className={`subscore-band ${subscoreBands.story.colorClass}`}>
                                                {subscoreBands.story.label}
                                            </span>
                                        )}
                                    </div>
                                    <div className="subscore" data-tooltip="Is the resume scannable? Can a recruiter get your value in 6 seconds?">
                                        <div className="subscore-value">{report.subscores?.readability || "‚Äî"}</div>
                                        <div className="subscore-label">Readability ‚ìò</div>
                                        {subscoreBands?.readability && (
                                            <span className={`subscore-band ${subscoreBands.readability.colorClass}`}>
                                                {subscoreBands.readability.label}
                                            </span>
                                        )}
                                    </div>
                                </div>

                                {/* Strengths */}
                                {report.strengths && report.strengths.length > 0 && (
                                    <div className="report-card">
                                        <div className="report-card-title">
                                            <svg className="report-card-title-icon" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                                            </svg>
                                            What&apos;s Working
                                        </div>
                                        <ul className="report-list">
                                            {report.strengths.map((s, i) => <li key={i}>{s}</li>)}
                                        </ul>
                                    </div>
                                )}

                                {/* Gaps */}
                                {report.gaps && report.gaps.length > 0 && (
                                    <div className="report-card">
                                        <div className="report-card-title">
                                            <svg className="report-card-title-icon" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                                            </svg>
                                            What&apos;s Missing
                                        </div>
                                        <ul className="report-list">
                                            {report.gaps.map((g, i) => <li key={i}>{g}</li>)}
                                        </ul>
                                    </div>
                                )}
                            </div>
                        )}

                        {/* Top Fixes */}
                        {activeTab === "fixes" && (
                            <div className="report-section active" style={{ width: '100%' }}>
                                <div className="report-card">
                                    <div className="report-card-title">Top Fixes ‚Äî Prioritized</div>
                                    <div className="top-fixes-list">
                                        {report.top_fixes?.map((fix, i) => (
                                            <div key={i} className="fix-item">
                                                <span className="fix-number">{i + 1}</span>
                                                <div className="fix-content">
                                                    <div className="fix-text">{fix.fix || fix.text}</div>
                                                    {fix.section_ref && <span className="fix-section-ref">{fix.section_ref}</span>}
                                                    {fix.impact_level && <span className={`fix-impact fix-impact-${fix.impact_level}`}>{fix.impact_level} impact</span>}
                                                </div>
                                            </div>
                                        )) || <p style={{ color: "var(--text-muted)" }}>No fixes available</p>}
                                    </div>
                                </div>
                            </div>
                        )}

                        {/* By Section */}
                        {activeTab === "sections" && (
                            <div className="report-section active" style={{ width: '100%' }}>
                                {report.section_review ? (
                                    Object.entries(report.section_review).map(([sectionName, section], i) => (
                                        <div key={i} className="section-review-card">
                                            <div className="section-review-header">
                                                <span className="section-review-name">{sectionName}</span>
                                                {section.grade && <span className={`section-review-grade grade-${section.grade?.replace('+', '-plus').replace('-', '-minus').toLowerCase()}`}>{section.grade}</span>}
                                            </div>
                                            <div className="section-review-body">
                                                {section.working && (
                                                    <div className="section-review-item section-review-working">
                                                        <span className="section-review-label">‚úì Working:</span>
                                                        <span className="section-review-text">{section.working}</span>
                                                    </div>
                                                )}
                                                {section.missing && (
                                                    <div className="section-review-item section-review-missing">
                                                        <span className="section-review-label">‚ñ≥ Missing:</span>
                                                        <span className="section-review-text">{section.missing}</span>
                                                    </div>
                                                )}
                                                {section.fix && (
                                                    <div className="section-review-item section-review-fix">
                                                        <span className="section-review-label">‚Üí Fix:</span>
                                                        <span className="section-review-text">{section.fix}</span>
                                                    </div>
                                                )}
                                                {section.priority && (
                                                    <div className="section-review-priority">
                                                        <span className={`priority-badge priority-${section.priority}`}>Priority: {section.priority}</span>
                                                    </div>
                                                )}
                                            </div>
                                        </div>
                                    ))
                                ) : (
                                    <p style={{ color: "var(--text-muted)", padding: "var(--space-xl)" }}>Section analysis not available</p>
                                )}
                            </div>
                        )}

                        {/* Bullet Upgrades */}
                        {activeTab === "rewrites" && (
                            <div className="report-section active" style={{ width: '100%' }}>
                                <div className="report-card">
                                    <div className="report-card-title">Bullet Upgrades</div>
                                    <div className="rewrites-container">
                                        {report.rewrites?.map((rw, i) => (
                                            <div key={i} className="rewrite-block">
                                                {rw.label && <div className="rewrite-category-label">{rw.label.toUpperCase()}</div>}
                                                <div className="rewrite-columns">
                                                    <div className="rewrite-column">
                                                        <div className="rewrite-label">Original</div>
                                                        <div className="rewrite-text">{rw.original}</div>
                                                    </div>
                                                    <div className="rewrite-column right">
                                                        <div className="rewrite-label">Better</div>
                                                        <div className="rewrite-text">{rw.better}</div>
                                                        {rw.enhancement_note && (
                                                            <div className="rewrite-enhancement-note">{rw.enhancement_note}</div>
                                                        )}
                                                    </div>
                                                </div>
                                            </div>
                                        )) || <p style={{ color: "var(--text-muted)" }}>No rewrites available</p>}
                                    </div>
                                </div>
                            </div>
                        )}

                        {/* Job Alignment */}
                        {activeTab === "alignment" && (
                            <div className="report-section active" style={{ width: '100%' }}>
                                {(report.job_alignment?.strongly_aligned?.length || report.job_alignment?.underplayed?.length || report.job_alignment?.missing?.length) ? (
                                    <>
                                        <div className="report-card">
                                            <div className="report-card-title">Theme Alignment</div>
                                            <div className="alignment-grid">
                                                <div className="alignment-col alignment-strong">
                                                    <div className="alignment-title">‚úì Strongly Aligned</div>
                                                    <ul className="alignment-list">
                                                        {report.job_alignment?.strongly_aligned?.map((item, i) => <li key={i}>{item}</li>) || <li style={{ color: "var(--text-muted)" }}>None found</li>}
                                                    </ul>
                                                </div>
                                                <div className="alignment-col alignment-under">
                                                    <div className="alignment-title">‚ö† Underplayed</div>
                                                    <ul className="alignment-list">
                                                        {report.job_alignment?.underplayed?.map((item, i) => <li key={i}>{item}</li>) || <li style={{ color: "var(--text-muted)" }}>None found</li>}
                                                    </ul>
                                                </div>
                                                <div className="alignment-col alignment-missing">
                                                    <div className="alignment-title">‚úó Missing/Weak</div>
                                                    <ul className="alignment-list">
                                                        {report.job_alignment?.missing?.map((item, i) => <li key={i}>{item}</li>) || <li style={{ color: "var(--text-muted)" }}>None found</li>}
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>

                                        {/* Role Fit */}
                                        {report.job_alignment?.role_fit && (
                                            <div className="report-card">
                                                <div className="report-card-title">Where You Read Strongest</div>
                                                <div className="role-fit-grid">
                                                    <div className="role-fit-col">
                                                        <div className="role-fit-title best-fit">Best Fit Roles</div>
                                                        <ul className="role-fit-list">
                                                            {report.job_alignment.role_fit.best_fit_roles?.map((role, i) => <li key={i}>{role}</li>)}
                                                        </ul>
                                                    </div>
                                                    <div className="role-fit-col">
                                                        <div className="role-fit-title stretch">Stretch Roles</div>
                                                        <ul className="role-fit-list">
                                                            {report.job_alignment.role_fit.stretch_roles?.map((role, i) => <li key={i}>{role}</li>)}
                                                        </ul>
                                                    </div>
                                                </div>
                                                <div className="role-fit-meta">
                                                    {report.job_alignment.role_fit.seniority_read && (
                                                        <div className="role-fit-meta-item">
                                                            <span className="role-fit-meta-label">Seniority Read</span>
                                                            <span className="role-fit-meta-value">{report.job_alignment.role_fit.seniority_read}</span>
                                                        </div>
                                                    )}
                                                    {report.job_alignment.role_fit.industry_signals && (
                                                        <div className="role-fit-meta-item">
                                                            <span className="role-fit-meta-label">Industry Signals</span>
                                                            <span className="role-fit-meta-value">{report.job_alignment.role_fit.industry_signals.join(", ")}</span>
                                                        </div>
                                                    )}
                                                    {report.job_alignment.role_fit.company_stage_fit && (
                                                        <div className="role-fit-meta-item">
                                                            <span className="role-fit-meta-label">Company Stage Fit</span>
                                                            <span className="role-fit-meta-value">{report.job_alignment.role_fit.company_stage_fit}</span>
                                                        </div>
                                                    )}
                                                </div>
                                            </div>
                                        )}

                                        {/* Positioning Suggestion */}
                                        {report.job_alignment?.positioning_suggestion && (
                                            <div className="report-card">
                                                <div className="report-card-title">Positioning Recommendation</div>
                                                <p className="positioning-text">{report.job_alignment.positioning_suggestion}</p>
                                            </div>
                                        )}
                                    </>
                                ) : (
                                    <div className="report-card" style={{ textAlign: "center" }}>
                                        <div style={{ color: "var(--text-muted)", padding: "var(--space-xl)" }}>
                                            <div style={{ fontSize: "var(--fs-lg)", marginBottom: "var(--space-xs)" }}>üìã</div>
                                            <div>Add a job description to see alignment analysis</div>
                                        </div>
                                    </div>
                                )}
                            </div>
                        )}

                        {/* Missing Wins */}
                        {activeTab === "wins" && (
                            <div className="report-section active" style={{ width: '100%' }}>
                                <div className="report-card">
                                    <div className="report-card-title">Surface Missing Wins</div>
                                    <p style={{ color: "var(--text-soft)", marginBottom: "var(--space-md)" }}>
                                        Answer these questions to uncover hidden impact your resume isn&apos;t telling yet:
                                    </p>
                                    <div className="ideas-list">
                                        {report.ideas?.questions?.map((q, i) => (
                                            <div key={i} className="ideas-section-card">
                                                <div className="ideas-section-title">{q.question}</div>
                                                {q.archetype && <span className="ideas-archetype">{q.archetype}</span>}
                                                {q.why && <p className="ideas-why">{q.why}</p>}
                                            </div>
                                        )) || (
                                                report.next_steps?.map((step, i) => (
                                                    <div key={i} className="ideas-section-card">
                                                        <div className="ideas-section-title">{step}</div>
                                                    </div>
                                                ))
                                            ) || <p style={{ color: "var(--text-muted)" }}>No questions available</p>}
                                    </div>
                                    {report.ideas?.how_to_use && (
                                        <div className="ideas-how-to-use">
                                            <strong>How to use:</strong> {report.ideas.how_to_use}
                                        </div>
                                    )}
                                </div>
                            </div>
                        )}
                    </>
                )}
            </div>
        </div>
    );
}
