"use client";

import { useState, useRef, ChangeEvent } from "react";

interface InputPanelProps {
    resumeText: string;
    jobDescription: string;
    onResumeTextChange: (text: string) => void;
    onJobDescChange: (text: string) => void;
    onFileSelect: (file: File) => void;
    onRun: () => void;
    isLoading: boolean;
    freeUsesRemaining: number;
}

export default function InputPanel({
    resumeText,
    jobDescription,
    onResumeTextChange,
    onJobDescChange,
    onFileSelect,
    onRun,
    isLoading,
    freeUsesRemaining
}: InputPanelProps) {
    const [fileName, setFileName] = useState<string | null>(null);
    const [isDragOver, setIsDragOver] = useState(false);
    const fileInputRef = useRef<HTMLInputElement>(null);

    const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (file) {
            setFileName(file.name);
            onFileSelect(file);
        }
    };

    const handleDrop = (e: React.DragEvent) => {
        e.preventDefault();
        setIsDragOver(false);
        const file = e.dataTransfer.files?.[0];
        if (file) {
            setFileName(file.name);
            onFileSelect(file);
        }
    };

    const handleRemoveFile = () => {
        setFileName(null);
        if (fileInputRef.current) {
            fileInputRef.current.value = "";
        }
    };

    const getRunHint = () => {
        if (freeUsesRemaining >= 2) {
            return "Your first 2 reports are free. No login required.";
        } else if (freeUsesRemaining === 1) {
            return "1 free report remaining";
        } else {
            return "Upgrade to keep analyzing";
        }
    };

    const charCount = resumeText.length;
    const isShortResume = charCount > 0 && charCount < 1500;

    return (
        <div className="input-panel">
            <div className="input-panel-content">
                {/* Resume Upload */}
                <div className="input-section">
                    <div className="input-label">
                        <span className="input-label-text">Your Resume</span>
                        <span className="input-label-hint">PDF, DOCX, or paste text</span>
                    </div>

                    <div
                        className={`dropzone ${isDragOver ? "dragover" : ""}`}
                        onClick={(e) => {
                            // Only trigger if clicking the dropzone itself, not the input
                            if (e.target === e.currentTarget || !(e.target as HTMLElement).closest('input')) {
                                fileInputRef.current?.click();
                            }
                        }}
                        onDragOver={(e) => { e.preventDefault(); setIsDragOver(true); }}
                        onDragLeave={() => setIsDragOver(false)}
                        onDrop={handleDrop}
                    >
                        <svg className="dropzone-icon" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5"
                                d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
                        </svg>
                        <div className="dropzone-text">Drop your resume here or click to upload</div>
                        <div className="dropzone-subtext">Supports PDF and DOCX files</div>
                        <input
                            type="file"
                            ref={fileInputRef}
                            className="dropzone-input"
                            accept=".pdf,.docx,application/pdf,application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                            onChange={handleFileChange}
                            onClick={(e) => e.stopPropagation()}
                        />
                    </div>

                    {fileName && (
                        <div className="dropzone-file-info">
                            <span className="dropzone-file-name">{fileName}</span>
                            <button className="dropzone-file-remove" onClick={handleRemoveFile}>‚úï Remove</button>
                        </div>
                    )}

                    <div style={{ textAlign: "center", margin: "var(--space-md) 0", color: "var(--text-muted)", fontSize: "var(--fs-small)" }}>
                        ‚Äî or paste ‚Äî
                    </div>

                    <div className="textarea-wrap">
                        <textarea
                            value={resumeText}
                            onChange={(e) => onResumeTextChange(e.target.value)}
                            placeholder="Paste your resume text here..."
                        />
                    </div>

                    {isShortResume && (
                        <div className="resume-warning">
                            <div className="resume-warning-icon">‚ö†Ô∏è</div>
                            <div className="resume-warning-content">
                                <div className="resume-warning-title">Resume looks short</div>
                                <div className="resume-warning-text">
                                    We found {charCount} characters. Most resumes have 1,500+ characters. You might get limited feedback.
                                </div>
                            </div>
                        </div>
                    )}
                </div>

                {/* Job Description (Optional) */}
                <div className="input-section">
                    <div className="input-label">
                        <span className="input-label-text">Job Description</span>
                        <span className="input-label-hint">Optional ‚Äî enables alignment analysis</span>
                    </div>
                    <div className="textarea-wrap">
                        <textarea
                            value={jobDescription}
                            onChange={(e) => onJobDescChange(e.target.value)}
                            className="textarea-small"
                            placeholder="Paste the job description to see how well your resume aligns..."
                        />
                    </div>
                </div>
            </div>

            {/* Run Button */}
            <div className="run-section">
                <button
                    className="btn-primary"
                    onClick={onRun}
                    disabled={isLoading || (!resumeText.trim() && !fileName)}
                >
                    <span>{isLoading ? "Analyzing..." : "See How Recruiters Read You"}</span>
                    <span>‚Üí</span>
                </button>
                <div className="run-hint">{getRunHint()}</div>
            </div>
        </div>
    );
}
