"use client";

import Link from "next/link";
import ThemeToggle from "../shared/ThemeToggle";

interface User {
    email?: string;
    firstName?: string;
}

interface WorkspaceHeaderProps {
    user?: User | null;
    onNewReport?: () => void;
    onSampleReport?: () => void;
    onSignIn?: () => void;
    onSignOut?: () => void;
    onHistory?: () => void;
}

export default function WorkspaceHeader({
    user,
    onNewReport,
    onSampleReport,
    onSignIn,
    onSignOut,
    onHistory
}: WorkspaceHeaderProps) {
    const userInitial = user?.firstName?.[0] || user?.email?.[0]?.toUpperCase() || "?";

    return (
        <header className="workspace-header">
            <Link href="/" className="workspace-brand">
                <span className="workspace-brand-name">Recruiter in Your Pocket</span>
            </Link>
            <div className="workspace-actions">
                <button className="btn-ghost" onClick={onSampleReport}>üìÑ Sample Report</button>
                <button className="new-report-btn" onClick={onNewReport}>
                    <svg width="16" height="16" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M12 8V16M8 12H16" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
                    </svg>
                    New Report
                </button>

                {!user ? (
                    <button className="btn-ghost sign-in-btn" onClick={onSignIn}>Sign In</button>
                ) : (
                    <div className="user-dropdown">
                        <button className="user-dropdown-btn" type="button">
                            <span className="user-avatar">{userInitial}</span>
                            <span className="user-email-display">{user.firstName || user.email}</span>
                            <span className="dropdown-arrow">‚ñæ</span>
                        </button>
                        <div className="user-dropdown-menu">
                            <div className="dropdown-item dropdown-email">{user.email}</div>
                            <div className="dropdown-divider" />
                            <button className="dropdown-item dropdown-link" onClick={onHistory}>
                                <svg className="dropdown-icon" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M3 7C3 4.239 4.239 3 7 3H17C19.761 3 21 4.239 21 7V17C21 19.761 19.761 21 17 21H7C4.239 21 3 19.761 3 17V7Z" stroke="currentColor" strokeWidth="1.5" />
                                    <path d="M7 8H17M7 12H17M7 16H13" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" />
                                </svg>
                                My Reports
                            </button>
                            <button className="dropdown-item dropdown-link" onClick={onNewReport}>
                                <svg className="dropdown-icon" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M12 8V16M8 12H16" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" />
                                </svg>
                                New Report
                            </button>
                            <div className="dropdown-divider" />
                            <Link href="/" className="dropdown-item dropdown-link">
                                <svg className="dropdown-icon" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M11 6L5 12M5 12L11 18M5 12H19" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" />
                                </svg>
                                Home
                            </Link>
                            <button className="dropdown-item sign-out-btn" onClick={onSignOut}>Sign Out</button>
                        </div>
                    </div>
                )}

                <ThemeToggle />
            </div>
        </header>
    );
}
